<style lang='less' scoped>
.home-lun {
  height: 1.5rem;
}

.home-lun_img {
  width: 100%;
  height: 100%;
}

.home-statistics {
  height: .28rem;
  background-color: #EFEFEF;
  display: flex;
  justify-content: space-between;
  padding: 0 .15rem;
  align-items: center;
}

.home-global {
  margin-top: .03rem;
}

.home-global_header {
  height: .3rem;
  background-color: #EFEFEF;
  padding: 0 .15rem;
  line-height: .3rem;
}

.home-scroll {
  position: fixed;
  top: 2.5rem;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  transition: all .2s;
  overflow: hidden;
}

.home-global_ul {
  padding-bottom: 1rem;
}

.home-global_li {
  padding: .1rem .15rem;
  border-bottom: 1px solid #eee;
  position: relative; // overflow: hidden;
  background-color: #fff;
}

.li-left_span {
  display: block;
  margin-bottom: .2rem;
}

.li-left_bottom {
  // display: flex;
  p {
    float: right;
    margin-right: .3rem;
  }
}

.li-right_icon {
  position: absolute;
  top: .23rem;
  right: .1rem;
  i {
    font-size: .2rem;
  }
}
</style>

<template>
  <div class="home">
    <mt-swipe :auto="4000" class="home-lun">
      <mt-swipe-item>
        <img class="home-lun_img" src="../../assets/img/longbo.jpg" />
      </mt-swipe-item>
      <mt-swipe-item>
        <img class="home-lun_img" src="../../assets/img/lunbo2.jpg" />
      </mt-swipe-item>
      <mt-swipe-item>
        <img class="home-lun_img" src="../../assets/img/kuan.jpg" />
      </mt-swipe-item>
    </mt-swipe>
    <div class="home-center">
      <div class="home-statistics">
        <div>矿机台数{{2}}台</div>
        <div>gpu数{{3}}个</div>
      </div>
      <div class="home-global">
        <div class="home-global_header">全局统计</div>
        <Scroll :data='list' class="home-scroll">
          <ul class="home-global_ul">
            <router-link 
            tag='li' 
            :to="{path: '/mineDetails', query: {id: item.id}}"  
            class="home-global_li" 
            v-for="(item, index) in list" 
            :key="item.id">
              <div class="global-li_left">
                <span class="li-left_span">{{item.name}}</span>
                <div class="li-left_bottom">
                  <span>gpu数{{item.gpu}}</span>
                  <span>{{item.mine}}</span>
                  <p>{{item.date}}</p>
                </div>
              </div>
              <div class="li-right_icon">
                <i class="iconfont icon-jiantouyou"></i>
              </div>
            </router-link>
          </ul>
        </Scroll>
      </div>
    </div>
  </div>
</template>

<script>
import Scroll from '@/components/scroll.vue'
export default {
  data () {
    return {
      list: [
        {
          name: '矿机1',
          gpu: 2,
          mine: '鱼池子',
          date: '2018.10.01',
          id: '12323'
        },
        {
          name: '矿机1',
          gpu: 2,
          mine: '鱼池子',
          date: '2018.10.01',
          id: '123323'
        },
        {
          name: '矿机1',
          gpu: 2,
          mine: '鱼池子',
          date: '2018.10.01',
          id: '121233'
        },
        {
          name: '矿机1',
          gpu: 2,
          mine: '鱼池子',
          date: '2018.10.01',
          id: '12123323'
        },
        {
          name: '矿机1',
          gpu: 2,
          mine: '鱼池子',
          date: '2018.10.01',
          id: '121232fs3'
        }
      ]
    }
  },
  components: {
    Scroll
  }
}
</script>


